import React from "react";
import {
  StyledCurrencyInput,
  StyledText,
  Wrapper,
  InputIcon,
  Container,
} from "./style";
import { Alert, TextInput, Text } from "react-native";
import { MaterialIcons } from "@expo/vector-icons";

import blueStyle from "../../../Styles/themes/blueStyle";

const colors = blueStyle.colors;

export default function CurrencyInput({
  onChangeText,
  inputText,
  text,
  placeholder,
  keyboardType,
  onPress,
}) {
  return (
    <Container>
      <StyledText>{text}</StyledText>
      <Wrapper>
        <StyledCurrencyInput
          value={inputText}
          placeholder={placeholder}
          onChangeValue={onChangeText}
          prefix="IN$ "
          unit="I"
          delimiter="."
          separator=","
          precision={3}
        />
      </Wrapper>
    </Container>
  );
}
