import React from "react";
import { InfoText, Wrapper, TitleText } from "./style";

export default function InfoCard({ title, text, id }) {
  return (
    <Wrapper key={id}>
      {title && <TitleText>{title}</TitleText>}
      {text && <InfoText haveTitle={title}>{text}</InfoText>}
    </Wrapper>
  );
}
