import React from "react";
import BalanceCard from "../../components/BalanceCard";
import BottomMenu from "../../components/BottomMenu";
import InfoCard from "../../components/InfoCard";
import Welcome from "../../components/Welcome";
import { Wrapper, Bottom, Container, InfoCardContainer } from "./style";
import { ScrollView, Text } from "react-native";
import blueStyle from "../../../Styles/themes/blueStyle";
import { infos } from "../../mock/home_infos";
const colors = blueStyle.colors;

export default function Home({ navigation }) {
  return (
    <Wrapper>
      <Container>
        <Welcome />
        <ScrollView
          contentContainerStyle={{
            flexGrow: 1,
            alignItems: "center",
            backgroundColor: "white",
          }}
          style={{ width: "100%" }}
        >
          <BalanceCard nav={navigation} />

          {infos.map((info) => (
            <InfoCardContainer key={info.id}>
              <InfoCard title={info.title} text={info.text} />
            </InfoCardContainer>
          ))}

          <Text style={{ marginTop: 300 }}>---</Text>
        </ScrollView>

        <Bottom>
          <BottomMenu navigation={navigation} />
        </Bottom>
      </Container>
    </Wrapper>
  );
}
