import React, { useContext, useEffect } from "react";
import { Alert } from "react-native";
import { Container, KeyboardAvoidingStyle, Padding } from "./style";
import TopMenu from "../../components/TopMenu";
import IconButton from "../../components/IconBottom";
import blueStyle from "../../../Styles/themes/blueStyle";
import GrayInput from "../../components/GrayInput";
import AuthContext from "../../context/auth";
import api from "../../api/api";
import LogoMenu from "../../assets/Image/LogoMenu";
import { regexEmail } from "../../utils/regexEmail";
import IconInput from "../../components/IconInput";
import {
  hasSpaceAtTheEnd,
  removeFinalChar,
} from "../../utils/removeFinalSpace";
const colors = blueStyle.colors;

export default function Login({ navigation }) {
  const [inputTextLogin, onChangeTextLogin] = React.useState("");
  const [inputTextPassword, onChangeTextPassword] = React.useState("");
  const [viewPass, onChangeViewPass] = React.useState(true);
  const { signed, handleSignIn } = useContext(AuthContext);

  useEffect(() => {
    if (hasSpaceAtTheEnd(inputTextLogin)) {
      onChangeTextLogin(removeFinalChar(inputTextLogin));
    }
  }, [inputTextLogin]);

  function checkLogin() {
    if (regexEmail(inputTextLogin)) {
      navigation.navigate("Loading");
      api
        .get(
          `info/{user}/login?username=${inputTextLogin}&password=${inputTextPassword}`
        )
        .then(function (response) {
          console.log("____________________________________________");
          console.log(response.data);
          handleSignIn(response.data);
        })
        .catch(function (error) {
          console.log(error);
          Alert.alert("Erro", "Nome ou senha Incorretos");
          navigation.pop();
        });
    } else {
      Alert.alert("Erro", "Login Invalido.");
    }
  }

  return (
    <Container>
      <TopMenu nav={navigation} hasArrow={false} name="Pgina de Login" />
      <KeyboardAvoidingStyle behavior="height">
        <Padding />
        <LogoMenu size={0.75} />
        <Padding />
        <GrayInput
          text="Nome de Usurio"
          placeholder="Nome de Usurio"
          onChangeText={onChangeTextLogin}
        ></GrayInput>
        {/* <GrayInput
          text="Senha"
          placeholder="Senha"
          onChangeText={onChangeTextPassword}
          secure={true}
          icon={"visibility"}
        ></GrayInput> */}
        <IconInput
          onChangeText={onChangeTextPassword}
          inputText={inputTextPassword}
          placeholder="Senha"
          text="Senha"
          icon="visibility"
          secure={viewPass}
          onPress={() => {
            onChangeViewPass(!viewPass);
          }}
        />
        <IconButton
          text="Entrar"
          // navigation={navigation}
          name="check"
          size={24}
          color="white"
          onPress={() => {
            checkLogin();
          }}
        />
        <IconButton
          text="Criar Conta"
          name="add"
          size={24}
          color="white"
          onPress={() => navigation.navigate("Create")}
        />
      </KeyboardAvoidingStyle>
    </Container>
  );
}
