import React, { useState, useEffect, useContext } from "react";
import {
  Wrapper,
  BalanceNumber,
  BalanceTitle,
  Bold,
  Medium,
  CachedIcon,
  RowIcons,
} from "./style";
import { MaterialIcons } from "@expo/vector-icons";
import blueStyle from "../../../Styles/themes/blueStyle";
import InaButton from "../InaButton";
import { Alert, Button } from "react-native";
import api from "../../api/api";
import currencyFormatHome from "../../utils/currencyFormat";
import  AuthContext  from "../../context/auth";

export default function BalanceCard({ nav }) {
  const blue1 = blueStyle.colors.blue1;

  const context = useContext(AuthContext);

  function getInaBallance() {
    const address = `${context.user["Wallet Address"]}`;
    api
      .get(`info/${address}/balance`)
      .then(function (response) {
        console.log(response);
        const fixNumber = 1000;
        const number = response.data / fixNumber;
        console.log(number);
        setBalance(number);
      })
      .catch(function (error) {
        console.log(error);
        // alert(error);
      });
    console.log(context.user["Wallet Address"])
  }

  function updateWallet() {
    setInterval(() => {
      getInaBallance();
    }, 10000);
  }

  useEffect(() => {
    getInaBallance();
    updateWallet();
  }, []);

  function handleCached() {
    Alert.alert("Atualizando");
    console.log("Atualizado");
    getInaBallance();
  }

  const [balance, setBalance] = useState(0.0);
  return (
    <Wrapper>
      <RowIcons>
        <MaterialIcons name="account-balance-wallet" size={30} color={blue1} />
      </RowIcons>

      <BalanceTitle>
        <Medium>INAC</Medium> Balance
      </BalanceTitle>
      <BalanceNumber>
        IN$ <Bold>{currencyFormatHome(balance)}</Bold>
      </BalanceNumber>
      <InaButton nav={nav} />
    </Wrapper>
  );
}
