import React, { useContext } from "react";
import { Linking, Text, Alert } from "react-native";
import { Wrapper, Container } from "./style";
import TopMenu from "../../components/TopMenu";
import IconButton from "../../components/IconBottom";
import blueStyle from "../../../Styles/themes/blueStyle";
import GrayInput from "../../components/GrayInput";
import AuthContext from "../../context/auth";
import { FontAwesome } from "@expo/vector-icons";
import IconButtonInsta from "../../components/IconButtonInsta";
const colors = blueStyle.colors;

export default function Logout({ navigation }) {
  const { signed, token, user, handleSignOut } = useContext(AuthContext);
  console.log(signed);
  const context = useContext(AuthContext);
  const key = context.user["Wallet Key"];

  return (
    <Container>
      <TopMenu nav={navigation} hasArrow={true} name="Opes" />
      <Wrapper>
        <IconButton
          text="Sair da Conta"
          // navigation={navigation}
          name="logout"
          size={24}
          color="white"
          onPress={() => handleSignOut()}
        />

        <IconButtonInsta
          text="Instagram Inacoin"
          // navigation={navigation}
          name="instagram"
          size={24}
          color="white"
          onPress={() =>
            Linking.openURL("https://www.instagram.com/inacoin_wallet/")
          }
        />
        <IconButton
          text="Ver Key Privada"
          // navigation={navigation}
          name="vpn-key"
          size={24}
          color="white"
          onPress={() => {Alert.alert("No compartilhe essa chave!", `${key}`)}}
        />
      </Wrapper>
    </Container>
  );
}
