import React from "react";
import { FontAwesome5 } from "@expo/vector-icons";
import { Ionicons } from "@expo/vector-icons";
import {
  Date,
  TypeText,
  Type,
  Wrapper,
  HeaderCard,
  ValueText,
  Wallet,
  BottomCard,
} from "./style";
import currencyFormat from "../../utils/currencyFormat";
import blueStyle from "../../../Styles/themes/blueStyle";

const colors = blueStyle.colors;

export default function Transaction({ date, value, wallet, wasReceived }) {
  return (
    <Wrapper>
      <HeaderCard>
        <Type>
          {wasReceived ? (
            <>
              <FontAwesome5 name="arrow-down" size={20} color={colors.green1} />
              <TypeText wasReceived={wasReceived}>Received</TypeText>
            </>
          ) : (
            <>
              <FontAwesome5 name="arrow-up" size={20} color={colors.red1} />
              <TypeText wasReceived={wasReceived}>Paid</TypeText>
            </>
          )}
        </Type>

        <Date>{date}</Date>

      </HeaderCard>
        <ValueText wasReceived={wasReceived}>{currencyFormat(value)}</ValueText>
      <BottomCard>
        <Ionicons name="wallet" size={24} color={colors.blue1} />
        <Wallet>{wallet}</Wallet>
      </BottomCard>
    </Wrapper>
  );
}
