import React, { useContext, Text } from "react";
import { Alert } from "react-native";
import { Container, KeyboardAvoidingStyle, Padding } from "./style";
import TopMenu from "../../components/TopMenu";
import IconButton from "../../components/IconBottom";
import blueStyle from "../../../Styles/themes/blueStyle";
import GrayInput from "../../components/GrayInput";
import AuthContext from "../../context/auth";
import api from "../../api/api";
import { regexEmail } from "../../utils/regexEmail";
const colors = blueStyle.colors;

function thereIsThisName(name) {
  return true;
}

export default function CreateAccount({ navigation }) {
  const [inputTextLogin, onChangeTextLogin] = React.useState("");
  const [inputTextPassword, onChangeTextPassword] = React.useState("");
  const [inputTextPasswordRepeat, onChangeTextPasswordRepeat] =
    React.useState(false);

  function handleMakeAccount() {
    if (regexEmail(inputTextLogin)) {
      if (inputTextPassword == "") {
        Alert.alert("Erro", "Senha Vazia");
      } else if (inputTextPassword != inputTextPasswordRepeat) {
        Alert.alert("Erro", "Senhas esto Diferentes");
      } else if (inputTextLogin == "") {
        Alert.alert("Erro", "nome Vazio");
      } else {
        api
          .post(`info/${inputTextLogin}/?password=${inputTextPassword}`)
          .then(function (response) {
            const data = response.data;
            if (data == "j existe esse nome") {
              Alert.alert("Erro", `Nome de usurio j est em uso.`);
            } else {
              Alert.alert("Sucesso", `Conta criada com sucesso!`);
            }
            navigation.pop();
          })
          .catch(function (error) {
            console.log(error);
            Alert.alert(error);
          });
      }
    } else {
      Alert.alert(
        "Erro",
        `Certifique-se que se nome de usurio tem de 5 a 16 caracteres, sem espaos em branco, acentuao nem caracteres especiais a no ser "_".`
      );
    }
  }

  return (
    <Container>
      <TopMenu nav={navigation} hasArrow={true} name="Pgina de Login" />

      <KeyboardAvoidingStyle behavior="height">
        <GrayInput
          text="Nome de Usurio"
          placeholder="Nome de Usurio"
          onChangeText={onChangeTextLogin}
        ></GrayInput>
        <GrayInput
          text="Senha"
          placeholder="Senha"
          secure={true}
          onChangeText={onChangeTextPassword}
        ></GrayInput>
        <GrayInput
          text="Senha (repetir)"
          placeholder="Senha"
          secure={true}
          onChangeText={onChangeTextPasswordRepeat}
        ></GrayInput>
        <IconButton
          text="Criar Conta"
          name="check"
          size={24}
          color="white"
          onPress={() => handleMakeAccount()}
        />
      </KeyboardAvoidingStyle>
    </Container>
  );
}
