import React from "react";
import { StyledButton, StyledText, ContainerButton } from "./style";
import { Alert, StyleSheet, View, Text } from "react-native";
import { MaterialIcons } from "@expo/vector-icons";
import blueStyle from "../../../Styles/themes/blueStyle";

const colors = blueStyle.colors;

export default function IconButtonInsta({
  name,
  size,
  color,
  onPress,
  text,
  navigation,
}) {
  function handlePress() {
    if (name && navigation) {
      navigation.navigate("Home");
    }
  }
  return (
    <ContainerButton>
      <StyledButton
        name={name}
        size={size}
        color={color}
        onPress={() => {
          onPress();
          handlePress();
        }}
      >
        <StyledText>{text}</StyledText>
      </StyledButton>
    </ContainerButton>
  );
}
