export function hasSpaceAtTheEnd(string) {
  const finalSpace = string.lastIndexOf(" ") + 1;
  const length = string.length;
  const result = finalSpace == length ? true : false;
  return result;
}

export function removeFinalChar(string) {
  const finalCharPosition = string.length - 1;
  const result = string.substr(0, finalCharPosition);
  return result;
}
