import React, { useState, useContext } from "react";
import { Alert, Clipboard, Text, View } from "react-native";
import {
  Bold,
  QrCodeImg,
  Title,
  Container,
  Wrapper,
  WalletAddress,
  Link,
  Icon,
  HelpText,
} from "./style";
import TopMenu from "../../components/TopMenu";
import blueStyle from "../../../Styles/themes/blueStyle";
import InfoCard from "../../components/InfoCard";
import AuthContext from "../../context/auth";
import QRCode from "react-native-qrcode-generator";
import IconButton from "../../components/IconBottom";

const colors = blueStyle.colors;

export default function Home({ navigation }) {
  const context = useContext(AuthContext);
  const linkAddress = context.user["Wallet Address"];
  let block;

  const copyToClipboard = async () => {
    Clipboard.setString(linkAddress);
    const text = await Clipboard.getString();
    Alert.alert("Texto Copiado", text);
  };

  const [on, setOn] = useState(false);

  //Variaveis para o InfoCard
  const title = " Compartilhe para receber pagamentos";
  const text = `Mande o endereo de carteira ou o QR code para quem vai te pagar. Clique no cdigo acima para copia-lo e made para seu amigo.`;
  if (on) {
    block = <Text>Carregando</Text>
    block = (
      <QRCode
        value={linkAddress}
        size={200}
        bgColor={blueStyle.colors.blue1}
        fgColor="white"
      />
    );
  } else {
    // block = <InfoCard text={" Clique no icone na parte superior direita para gerar o Qr Code."}/>
    block = (
      <IconButton
      name="qr-code-2"
        text="Gerar QR Code"
        color="white"
        size={30}
        onPress={() => {setOn(true)}}
      />
    );
  }

  return (
    <Container>
      <TopMenu
        nav={navigation}
        name="Receber"
        icon="qr-code-2"
        iconOnPress={() => setOn(true)}
      />
      <Wrapper>
        <Title>
          Esta  sua <Bold>Carteira Inacoin</Bold>
        </Title>

        {block}

        <WalletAddress onPress={() => copyToClipboard()}>
          <Link>{linkAddress}</Link>
          <Icon name="content-copy" size={24} />
        </WalletAddress>

        <InfoCard title={title} text={text} />
      </Wrapper>
    </Container>
  );
}
