import styled from "styled-components/native";
import blueStyle from "../../../Styles/themes/blueStyle";
const colors = blueStyle.colors;

export const Wrapper = styled.View`
  flex: 1;
  background-color: ${colors.blue2};
  align-items: center;
`;

export const Container = styled.View`
  flex: 1;
  position: relative;
  align-items: center;
  width: 90%; //tamanho da area segura da tela
`;

export const Bottom = styled.View`
  width: 100%;
  position: absolute;
  bottom: 0px;

  flex: 1;
  display: flex;
  align-items: center;

  background-color: ${colors.blue2};
`;

export const InfoCardContainer = styled.View`
  width: 100%;
  align-items: center;
  padding: 0 30px 0 30px;
  margin: 10px 0 0 0;
  background-color: white;
`;
