import { MaterialIcons } from "@expo/vector-icons";
import React from "react";
import { Alert } from "react-native";
import blueStyle from "../../../Styles/themes/blueStyle";
import {
  Wrapper,
  StyledText,
  StyledMaterialIconsLeft,
  StyledMaterialIconsRight,
} from "./style";
const colors = blueStyle.colors;

export default function TopMenu({
  nav,
  name,
  icon,
  iconOnPress,
  hasArrow = true,
}) {
  return (
    <Wrapper>
      {hasArrow && (
        <StyledMaterialIconsLeft
          name="arrow-back"
          size={30}
          color="white"
          onStartShouldSetResponder={() => nav.popToTop()}
        />
      )}

      <StyledText>{name}</StyledText>
      {icon && (
        <StyledMaterialIconsRight
          name={icon}
          size={32}
          color={colors.white}
          onPress={iconOnPress}
        />
      )}
    </Wrapper>
  );
}
