import React from "react";
import {Alert} from "react-native";
import { ButtonText, Container, Wrapper, ComingSoon } from "./style";
import { MaterialIcons } from "@expo/vector-icons";
import blueStyle from "../../../Styles/themes/blueStyle";

const colors = blueStyle.colors;

export default function InaButton({ nav }) {
  return (
    <Container>
      <Wrapper onStartShouldSetResponder={() => Alert.alert("Em breve!", "Estamos trabalhando em atualizaes.")}>
        <MaterialIcons name="history" size={24} color={colors.gray3} />
        <ButtonText>Histrico de Transaes</ButtonText>
      </Wrapper>
    </Container>
  );
}
