import React from "react";
import {
  WalletInput,
  StyledCurrencyInput,
  StyledText,
  Wrapper,
  InputIcon,
  Container,
} from "./style";
import { Alert, TextInput, Text } from "react-native";
import { MaterialIcons } from "@expo/vector-icons";

import blueStyle from "../../../Styles/themes/blueStyle";

const colors = blueStyle.colors;

export default function GrayInput({
  onChangeText,
  inputText,
  text,
  placeholder,
  keyboardType,
  icon, //boolean
  onPress,
  currency, //boolean
  secure
}) {
  return (
    <Container>
      <StyledText>{text}</StyledText>
      <Wrapper>
        <WalletInput
          onChangeText={onChangeText}
          value={inputText}
          placeholder={placeholder}
          keyboardType={keyboardType}
          icon={icon}
          secureTextEntry={secure}
        />

        {icon && inputText == false && (
          <InputIcon
            name={icon}
            size={24}
            color={colors.blue1}
            onPress={onPress}
          />
        )}
      </Wrapper>
    </Container>
  );
}