import styled from "styled-components";
import blueStyle from "../../../Styles/themes/blueStyle";

const colors = blueStyle.colors;

export const Container = styled.View`
  display: flex;
  align-items: flex-start;
  justify-content: center;
`;

export const Wrapper = styled.View`
  margin-top: 30px;
  border: ${colors.gray3} 1px;
  padding: 7px 15px;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  border-radius: 3px;
`;

export const ButtonText = styled.Text`
  font-size: 16px;
  margin-left: 10px;

  color: ${colors.gray3};
`;

export const ComingSoon = styled.Text`
  font-size: 16px;
  margin-left: 10px;
  color: ${colors.gray3};
`;
