import styled from "styled-components";
import blueStyle from "../../../Styles/themes/blueStyle";
import { MaterialIcons } from "@expo/vector-icons";

const colors = blueStyle.colors;

export const Wrapper = styled.View`
  z-index: 1;
  width: 100%;
  height: 45px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: relative;

  background-color: ${colors.blue2};
`;
export const StyledMaterialIconsLeft = styled(MaterialIcons)`
  position: absolute;
  left: 5%;
`;
export const StyledMaterialIconsRight = styled(MaterialIcons)`
  position: absolute;
  right: 5%;
`;
export const StyledText = styled.Text`
  font-family: "Roboto_500Medium";
  font-size: 16px;
  color: ${colors.white};
`;
