import styled from "styled-components";
import {Text} from "react-native";
import blueStyle from "../../../Styles/themes/blueStyle";
const colors = blueStyle.colors;

export const Wrapper = styled.View`
  width:360px;
  /* height: 100px; */
  margin: 5px;

  background-color: ${colors.white};
  padding: 15px;
  border-radius: 15px;

`;

export const HeaderCard = styled.View`
  flex-direction: row;
  justify-content: space-between;
`;

export const Type = styled.View`
  flex-direction: row;
  align-items: center;
  width: 100px;

`;

export const TypeText = styled.Text`
  font-family: "Roboto_400Regular";
  font-size: 16px;
  color: ${props => props.wasReceived ? colors.green1 : colors.red1};
  margin-left: 10px;
`;

export const Date = styled.Text`
  font-family: "Roboto_400Regular";
  font-size: 16px;
 
`;

export const ValueText = styled.Text`
  font-family: "Roboto_400Regular";
  font-size: 16px;

  color: ${props => props.wasReceived ? colors.green1 : colors.red1};
`;

export const Wallet = styled.Text`
  width: 90%;

  margin-left: 10px;

  font-family: "Roboto_400Regular";
  font-size: 16px;

  text-decoration: underline;
  color: ${colors.blue1};
`;

export const BottomCard = styled.View`
  margin-top: 10px;
  flex-direction: row;
  /* justify-content: center; */
  align-items: center; 
`;


