import React, { useState, useContext } from "react";
import { Alert, Clipboard } from "react-native";
import {
  Wrapper,
  ContainerWallet,
  ContainerValue,
  ContainerInfo,
  Container,
  InfoContainer,
} from "./style";
import TopMenu from "../../components/TopMenu";
import IconButton from "../../components/IconBottom";
import IconInput from "../../components/IconInput";
import CurrencyInput from "../../components/CurrencyInput";
import InfoCard from "../../components/InfoCard";
import blueStyle from "../../../Styles/themes/blueStyle";
import api from "../../api/api";
import AuthContext from "../../context/auth";
import Scanner from "../Scanner";
import { checkWallet } from "../../utils/depositCheckInputs";

const colors = blueStyle.colors;
const title = " Pague atravs do QR code ou do cdigo da carteira";
const text = ` Clique no cone  esquerda do cabealho para ler o QR code de seu amigo ou, se preferir, no cone dentro da caixa de texto para colar o endereo de carteira. Em seguida, digite o valor e clique em confirmar para efetuar a transao. `;
export default function Deposit({ navigation }) {
  const [inputTextWallet, onChangeTextWallet] = useState("");
  const [inputTextValue, onChangeTextValue] = useState("");
  const [qrOn, setQrOn] = useState(false);
  let page;

  const context = useContext(AuthContext);
  const myWallet = context.user["Wallet Address"];
  const key = context.user["Wallet Key"];

  const fetchCopiedText = async () => {
    const text = await Clipboard.getString();
    onChangeTextWallet(text);
  };

  function transaction(depositWallet, receiveWallet, depositKey, value) {
    const query = `info/{user}/transaction?addressFrom=${depositWallet}&addressTo=${receiveWallet}&key=${depositKey}&value=${value}`;

    api
      .post(query)
      .then(function (response) {
        // console.log(response);
        navigation.pop();
      })
      .catch(function (error) {
        console.log(error);
      });
  }

  function getWalletByQrCode(Wallet) {
    onChangeTextWallet(Wallet);
  }

  function handleOnPress() {
    if (checkWallet(inputTextWallet)) {
      return Alert.alert(
        "Ateno",
        "Tem certeza que quer fazer essa transao ?",
        [
          {
            text: "sim",
            onPress: () => {
              transaction(myWallet, inputTextWallet, key, inputTextValue);
              Alert.alert(
                "Transao em Progresso",
                "Sua transao ser realizada em instantes"
              );
            },
          },
          { text: "no", onPress: () => {} },
        ]
      );
    } else {
      Alert.alert("ERRO", "Cdigo da carteira invalido");
    }
  }

  if (qrOn) {
    page = <Scanner getWalletByQrCode={getWalletByQrCode} setQrOn={setQrOn} />;
  } else {
    page = (
      <Container>
        <TopMenu
          nav={navigation}
          icon={"qr-code-scanner"}
          iconOnPress={() => setQrOn(true)}
          name="Depositar"
          // iconOnPress={() => navigation.navigate()}
        />
        <Wrapper>
          <IconInput
            onChangeText={onChangeTextWallet}
            inputText={inputTextWallet}
            placeholder="Cdigo da Carteira"
            text="Cdigo da Carteira"
            icon="content-paste"
            onPress={() => fetchCopiedText()}
          />

          <CurrencyInput
            mask="inacoin"
            onChangeText={onChangeTextValue}
            inputText={inputTextValue}
            placeholder="Valor"
            text="Valor"
            keyboardType="numeric"
            currency={true}
          />

          <IconButton
            name="check"
            size={24}
            color="white"
            onPress={() => {
              handleOnPress();
            }}
            text="Confirmar"
          />
          <InfoContainer>
            <InfoCard text={text} title={title} />
          </InfoContainer>
        </Wrapper>
      </Container>
    );
  }

  return page;
}
