import styled from "styled-components";
import blueStyle from "../../../Styles/themes/blueStyle";
import {  TouchableOpacity } from "react-native";
const colors = blueStyle.colors;

export const Wrapper = styled.TouchableOpacity`
  align-items: center;
  justify-content: space-between;

  background-color: ${(props) => (props.home ? colors.blue7 : colors.blue6)};
  padding: 20px;
  width: 110px;

  min-height: 150px;
  max-height: 150px;

  border-radius: 3px;

  margin-right: 10px;

`;

export const CardText = styled.Text`
 

  color: ${colors.white};
`;
